body input:focus:required:invalid,
body textarea:focus:required:invalid {
  outline: none; }
body input:required:valid,
body textarea:required:valid {
  outline: none; }
a:active:focus {
  outline: none; }

html, body {
  height: 100%; }

body {
  font-size: 16px;
  position: relative;
  line-height: 1.2;
  font-family: $roboto;
  font-weight: 400;
  background: $white;
  //overflow-x: hidden
  opacity: 1;
  -webkit-font-smoothing: antialiased;
  -webkit-appearance: none; }

.main-wrapper {
  overflow-x: hidden; }

h1 {
  font-size: 64px; }

h2 {
  font-size: 36px; }

h3 {
  font-size: 28px; }

h4 {
  font-size: 24px; }

h5 {
  font-size: 16px; }

h6 {
  font-size: 21px; }

p {
  font-size: 16px; }

p.ps {
  font-size: 14px; }

ul {
  list-style-type: none; }

.container {
  max-width: 1300px;
  margin: 0 auto;
  box-sizing: border-box;
  @include respond-from(1366, 1201) {
    max-width: 1140px; }
  @include respond-from(1200, 993) {
    max-width: 960px; }
  @include respond-from(992, 759) {
    max-width: 750px; }
  @include respond-to(758) {
    max-width: 100%;
    padding: 0 15px; } }

.header-container {
  padding: 0 35px;
  margin: 0 auto;
  @include respond-from(1366, 1201) {
    max-width: 1140px;
    padding: 0; }
  @include respond-from(1200, 993) {
    max-width: 960px; }
  @include respond-from(992, 759) {
    max-width: 750px; }
  @include respond-to(758) {
    max-width: 100%;
    padding: 0 15px; } }

.btn {
  font-family: $oswald;
  font-size: 20px;
  color: $white;
  background-color: $brand-orange;
  text-transform: uppercase;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  @include respond-to(992) {
    font-size: 16px; } }

.block_flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  @include respond-to(768) {
    justify-content: center;
    flex-direction: column;
    flex-wrap: nowrap; } }

.close {
  position: absolute;
  top: 10px;
  left: 15px;
  cursor: pointer; }


