.footer_logo {
  display: inline-block;
  float: left;
  margin-right: 112px;
  @include respond-to(768) {
    display: flex;
    justify-content: center;
    float: none;
    margin-right: 0; }
  &:after {
    content: "";
    clear: both; }
  a {
    display: inline-block;
    height: 56px;
    @include respond-to(576);
    margin: 0 auto;
    object {
      pointer-events: none; } } }
