.header {
  width: 100%;
  height: 110px;
  background: $brand-minor;
  padding-top: 28px;
  box-sizing: border-box;
  @include respond-to(992) {
    height: 68px;
    padding-top: 15px; }
  @include respond-to(578) {
    height: 52px; }
  .block_right {
    float: right;
    .phone {
      font-family: $oswald;
      font-size: 20px;
      color: $white;
      text-decoration: none;
      letter-spacing: 1.9px;
      line-height: 55px;
      margin-right: 39px;
      @include respond-to(1200) {
        display: none; } }
    .phone_mob {
      display: none;
      border: 1px solid $white;
      border-radius: 3px;
      padding: 14px 16px;
      margin-top: -3px;
      margin-right: 20px;
      float: left;
      @include respond-to(992) {
        padding: 7px 9px; }
      @include respond-to(576) {
        padding: 6px 8px;
        margin-right: 7px; }
      i {
        color: $white;
        font-size: 28px;
        @include respond-to(992) {
          font-size: 20px; }
        @include respond-to(576) {
          font-size: 18px; } }
      @include respond-to(1200) {
        display: inline-block; } } }
  &__btn {
    float: right;
    padding: 14px 53px;
    margin-top: -4px;
    @include respond-to(992) {
      padding: 11px 53px; }
    @include respond-to(768) {
      padding: 11px 32px; }
    @include respond-to(576) {
      padding: 5px 15px; } } }
