.block_logo {
  display: inline-block;
  float: left;
  margin-right: 112px;
  @include respond-to(1366) {
    margin-right: 42px; }
  @include respond-to(1014) {
    margin-right: 14px; }
  &:after {
    content: "";
    clear: both; }
  a {
    display: inline-block;
    height: 56px;
    object {
      pointer-events: none;
      @include respond-to(992) {
        width: auto;
        height: 40px; }
      @include respond-to(576) {
        height: 24px; } } } }

