.mobile_menu {
  width: 290px;
  height: 100vh;
  position: fixed;
  top: 0;
  right: -290px;
  background-color: $grey30;
  padding-top: 40px;
  padding-left: 15px;
  box-sizing: border-box;
  .mob__block_nav {
    position: relative;
    border-left: 1px solid $brand-minor; }
  &__menu {
    display:  inline-block;
    padding-left: 15px;
    li {
      display: block;
      font-family: $oswald;
      font-weight: 400;
      font-size: 16px;
      line-height: 25px;
      a {
        text-decoration: none;
        color: $brand-minor;
        transition: .3s ease;
        &:hover {
          color: $brand-orange; } } } }
  &__sub_menu__menu {
    padding-left: 15px;
    li {
      font-size: 14px; } } }
