.block_nav {
  display: inline-block;
  @include respond-to(992) {
    display: none; }
  &__menu {
    display: inline-block;
    li {
      display: inline-block;
      font-family: $oswald;
      font-weight: 400;
      font-size: 20px;
      text-transform: uppercase;
      margin-right: 33px;
      letter-spacing: 1.9px;
      line-height: 55px;
      a {
        text-decoration: none;
        color: $white;
        transition: .3s ease;
        &:hover {
          color: $brand-orange; }
        &:hover i {
          transform: rotate(-90deg);
          color: $brand-orange; } } }
    .sub_menu {
      position: relative;
      i {
        color: $white;
        font-size: 22px;
        margin-left: 7px;
        transition: .4s ease; }
      &__menu {
        display: none;
        position: absolute;
        background-color: $grey30;
        top: 55px;
        left: 0;
        padding: 15px 0 15px 15px;
        box-shadow: 3px 3px 5px 0px rgba(0,0,0,0.36);
        width: 253px;
        box-sizing: border-box;
        li {
          display: block;
          font-size: 15px;
          line-height: 25px;
          text-transform: none;
          letter-spacing: normal;
          a {
            color: $brand-minor;
            &:hover {
              color: $brand-orange; } } } } } } }




