.footer {
  width: 100%;
  background-color: $brand-minor;
  padding-top: 66px;
  box-sizing: border-box;
  &__item {
    color: $white;
    width: 23%;
    min-width: 262px;
    @include respond-to(768) {
      margin: 0 auto; } }
  &__top {
    padding-bottom: 54px;
    width: 100%;
    @include respond-to(768) {
      text-align: center; } }
  &__title {
    font-family: $oswald;
    font-weight: 400;
    font-size: 23px;
    color: $white;
    text-transform: uppercase;
    letter-spacing: 1px;
    margin-top: 4px; }
  &__subtitle {
    font-size: 14px;
    margin-top: 9px; }
  &__phone {
    color: $brand-orange !important;
    font-size: 27px;
    font-weight: 500;
    letter-spacing: 1.8px; }
  &__content {
    margin-top: 22px;
    font-family: $oswald;
    font-weight: 400;
    font-size: 22px;
    color: $white;
    text-transform: uppercase;
    letter-spacing: 1px;
    a {
      color: $white;
      text-decoration: none; } }
  &__menu {
    li {
      padding-bottom: 12px; } }
  &__form {
    input, button {
      display: block;
      width: 100%;
      height: 40px;
      box-sizing: border-box;
      margin-top: 16px; }
    input {
      border: none;
      padding: 0 10px; } }
  &__btn {
    border-radius: 2px;
    margin-top: 13px;
    font-size: 16px; } }
.copyright {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  font-family: $roboto;
  font-weight: 400;
  color: $white;
  border-top: 1px solid $white;
  line-height: 67px;
  @include respond-to(768) {
    justify-content: center;
    text-align: center;
    line-height: 1.2;
    padding-top: 25px; }
  p {
    font-size: 14px;
    padding-bottom: 25px; }
  span {
    color: $brand-orange; } }


.item1 {
  order: 0;
  @include respond-to(768) {
    order: 0; } }

.item2 {
  order: 0;
  @include respond-to(768) {
    order: 1;
    padding-top: 58px; } }

.item3 {
  order: 1;
  @include respond-to(768) {
    order: 0;
    padding-top: 50px; } }

.item4 {
  order: 2;
  padding-top: 55px; }
